body{
    background: #EFEFF4;
}
.lf{float: left}
.rf{float: right}
.list-search_box{
    padding: 7px 5%;
    background: #2F2F2F;
    overflow: hidden;
}
.list-back{
    display: block;
    width:10%;
    height:30px;
    background: url("../../assets/images/back.png") no-repeat 0 center;
    background-size: 15px 20px;
}
.list-search{
    display: block;
    width:90%;
    padding: 0 36px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    color: #B5B5B5;
    border-radius: 4px;
    background: #EBEBEB url("../../assets/images/search.png") no-repeat 9px center;
    background-size: 18px;
}

.list-navbar{
    text-align: center;
    background: #DCDCDC;
    overflow: hidden;
}
.list-navbar a{
    display: block;
    color: #747474;
    float: left;
    width:25%;
    height:40px;
    line-height: 40px;
    border-bottom: 1px solid #BB0200;
}
.list-navbar a:not(:last-child){
    background: url("../../assets/images/line-row.jpg") right center no-repeat;
    background-size: 2%;
}
.list-navbar a.active{
    color: #C40000;
    border-bottom: 3px solid #C40000;
}
.list-sort{
    display: inline-block;
    position: relative;
    top:6px;
}
.list-sort .list-sort_up,.list-sort .list-sort_down{
    width:0;height:0;
    border:5px solid transparent;
}
.list-list-sort .list-sort_up{
    border-bottom-color:#BD0000;
}
.list-sort .list-sort_down{
    border-top-color: #A0A0A0;
    margin-top: 2px;
}
/**************右侧栏******************/
.list-side_right{
    background: #EFEFF4;
    overflow: hidden;
    position: fixed;
    width: 260px;
    top: 0;
    z-index: 10;
    right:-260px;
    height: 100%;
}
.list-navbar_right{
    height:44px;
    line-height: 44px;
    background: #2F2F2F;
    color: #fff;
    padding: 0 10px;
    margin-bottom: 40px;
    font-size: 16px;
}
.list-navbar_right p{
    display: inline-block;
    width:76%;
    text-align: center;
}
.list-navbar_right a{
    font-size: 14px;
    color: #fff;
}
.list-condition{
}
.list-condition ul{
    overflow: hidden;
    background: #fff;
    padding-left: 10px;
}
.list-condition ul li{
    height:44px;
    line-height: 44px;
    border-bottom: 1px solid #EEEEEE;
}
.list-condition ul li div{
    display: inline-block;
    margin-right: 10px;
    text-align: right;
    font-weight: 600;
    color: #8E8E93;
    font-size: 14px;
    line-height: 41px;
    background: url("../../assets/images/right.png") no-repeat right center;
    background-size: 9px 12px;
    padding-right: 15px;
}
.list-condition ul li div span{
    font-size: 12px;
    font-weight: 400;
}

/*one-three*/

.list-top{
    height:44px;
    line-height: 44px;
    background: #2F2F2F;
    text-align: center;
    color: #fff;
    font-size: 16px;
    overflow: hidden;
}
.list-top .list-to_one{
    display: block;
    width:15px;
    height:44px;
    background: url("../../assets/images/back.png") no-repeat  center;
    background-size: 15px 20px;
    margin-left: 15px;
}
.list-top span{
    position: relative;
    left: -15px;
}
.list-one_one,.list-one_two{
    display: none;
}
.list-one_two ul,.list-one_one ul.list-category_list{
    background: #fff;
    padding-left: 10px;
    margin: 40px 0;
    overflow-y:scroll ;
}
.list-one_two ul li,.list-one_one ul.list-category_list li.list-category_list_item{
    height:44px;
    line-height: 44px;
}
.list-one_one ul.list-category_list li.list-category_list_item:not(:first-child){
    background: url("../../assets/images/right.png") no-repeat 225px center;
    background-size: 9px 12px;
}
.list-one_one ul.list-category_list li.list-category_list_item:not(:first-child).list-select{
    /* background: url("../../assets/images/down.png") no-repeat 223px 17px; */
    background-size: 12px 9px;
    border-bottom: 1px solid #C8C7CC;
    height:auto;
}
.list-one_two ul li.list-select,.list-one_one ul.list-category_list li.list-category_list_item:first-child.list-select,.list-category_list_item ul li.list-select{
    background: url("../../assets/images/gou.png") no-repeat 225px center;
    background-size: auto;
}
.list-one_two ul li:not(:last-child),.list-one_one ul.list-category_list li.list-category_list_item:not(:last-child),.list-category_list_item ul li:not(:last-child){
    border-bottom: 1px solid #EEEEEE;
}
.list-category_list_item ul{
    display: none;
}
/****************************************************/
.list-list_show{
    background: #EFEFF4;
    overflow: hidden;
    padding: 10px 0;
    overflow-y: scroll;
}
.list-product_box{
    float: left;
    width:49%;
    padding-left: 7px;
    margin-bottom: 6px;
}
.list-product{
    background: #fff;
    border:1px solid #DFDFDF;

}
.list-product img{
    display: block;
    width:90%;
    height:165px;
    margin:10px auto;
}
.list-product a{
    color: #333333;
}
.list-jieshao{
    padding: 0 5%;
    height:49px;
    overflow: hidden;
}
.list-jiage{
    margin:5px 0 10px;
    padding: 0 5%;
    color: #DD0707;
    position: relative;
}
.list-jiage span{
    color: #A9A9A9;
    font-size:12px;
    position: absolute;
    right:5%;
}
.list-none {
    display: none;
    background: #EFEFF4;
}
.list-none img{
    display: block;
    margin:30px auto 20px;
}
.list-none p{
    text-align: center;
}
.list-none p:last-child{
    color: #969696;
    margin-top: 5px;
}