/***************全局样式********************/
*{
	box-sizing: border-box;
	padding:0;
	margin: 0;
	list-style:none;
	text-decoration:none;
	outline: none;
}
.lf{float: left}
.rf{float: right}
body{
	font:14px  Helvetica Neue,Helvetica,sans-serif,"Microsoft yahei",Arial;
}
/***************尾部样式********************/
footer{
	display: -webkit-box;
	background: #2F2F2F;
	width:100%;
	height:55px;
	position:fixed;
	bottom:0;
}
footer a{
	display: block;
	font-size: 12px;
	width:25%;
	height:55px;
	padding-top: 33px;
	text-align: center;
	background: no-repeat center 12px;
	background-size: 21px;
	color: #C4C4C4;
}
footer a.active{
	background-color: #ED3037;
	color: #FCFAFA;
}
footer a:nth-child(1){
	/*background-position-y: 12px;*/
	background-image:url(data:image/png;base64,);
}
footer a:nth-child(2){
	/*background-position-y: 11px;*/
	background-image:url(data:image/png;base64,);
}
footer a:nth-child(3){
	background-image:url(data:image/png;base64,);
	/*background-position-y: 14px;*/
}
footer a:nth-child(4){
	/*background-position-y: 12px;*/
	background-image:url(data:image/png;base64,);
}
footer a.active:nth-child(1){
	background-image:url(data:image/png;base64,);
}
footer a.active:nth-child(2){
	background-image:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAFAAAAA8BAMAAAADCSC/AAAAG1BMVEX///////////////////////////////////8HBRbfAAAACHRSTlMA7QaR7pCIiUMi3/cAAABcSURBVHhe7ZPBCYAwFENVcIfenUDpEI7gOgWxGVu+Ch+rgp68vHcJhEDIIdULoAvzYNr0Kth8Z5IW01YXzHeClExrXUgfg16dH6rzecx4jIk3Y/4A+AyfAeAzDqy1Js1UnXHKvwAAAABJRU5ErkJggg==);
}
footer a.active:nth-child(3){
	background-image:url(data:image/png;base64,);
}
footer a.active:nth-child(4){
	background-image:url(data:image/png;base64,);
}

