body{background: #EEEEEE}
.buy-top{
    width:100%;
    height:44px;
    line-height: 44px;
    background: #2F2F2F;
    text-align: center;
    color: #fff;
    font-size: 16px;
    overflow: hidden;
    padding: 0 15px;
    position:fixed;
    top:0;
    z-index: 10;
}
.buy-top .buy-back{
    display: block;
    float: left;
    width:15px;
    height:44px;
    background: url("../../assets/images/back.png") no-repeat  center;
    background-size: 15px 20px;
}
.buy-top span{
    position: relative;
    left: 5px;
}
.buy-top .buy-edit{
    float: right;
    display: block;
    color: #fff;
}

.buy-main{
    margin:44px 0 95px;
    /* overflow: hidden; */
}
.buy-product {
    margin-bottom: 10px;
}
.buy-title{
    height:40px;
    line-height: 40px;
    width: 120%;
    border-top: 1px solid #EEEEEE;
    background: #fff url("../../assets/images/xuanzhong.png") no-repeat 10px center;
    background-size: 20px 20px;
    padding-left: 38px;
}
.buy-title.buy-select{
    background: #fff url("../../assets/images/xuanzhong.png") no-repeat 10px center;
    background-size: 20px 20px;
}
.buy-commodity_box{
    width:100%;
    position: relative;
}
.buy-commodity{
    /*width:100%;*/
    border-top: 1px solid #EEEEEE;
    font-size: 12px;
    padding: 10px 0 15px 38px;
    overflow: hidden;
    background: #fff url("../../assets/images/xuanzhong.png ") no-repeat 10px center;
    background-size: 20px 20px;
    position: relative;
    right:0;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
}
.buy-commodity.buy-select{
    background: #fff url("../../assets/images/xuanzhong.png") no-repeat 10px center;
    background-size: 20px 20px;
}
.buy-commodity .buy-img{
    width:30%;
}
.buy-commodity .buy-img img{
    display: block;
    width:100%;
    height:90px;
}
.buy-commodity .buy-detail{
    width:70%;
    padding: 0 7px;
}
.buy-jieshao{
    width:100%;
    height:48px;
    overflow: hidden;
    font-size: 12px;
}
.buy-size{
    width:100%;
    height:18px;
    line-height: 18px;
    overflow: hidden;
    color: #CCCCCC;
}
.buy-price_num{
    overflow: hidden;
    margin-right: 5px;
}
.buy-price{
    height: 24px;
    line-height: 35px;
    color: #DD0707;
    font:14px "Microsoft yahei",Arial;
}
.buy-num{
    overflow: hidden;
}
.buy-num span{
    text-align: center;
    line-height: 22px;
}
.buy-num button,.buy-num span{
    float: left;
    width:24px;
    height:24px;
    border: 1px solid #DFDFDF;
    background: #fff;
    color: #000;
    text-align: center;
    line-height: 24px;

}
.buy-del{
    width: 60px;
    height: 115px;
    color: #fff;
    background: #FF3B30;
    line-height: 90px;
    text-align: center;
    position: absolute;
    right: -60px;
    top: 1px;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
}
.buy-pay{
    /* display: none; */
    overflow: hidden;
    position: fixed;
    z-index: 1;
    bottom: 55px;
    width:100%;
    height: 40px;
    line-height: 40px;
}
.buy-pay p,.buy-pay a{
    display: block;
    float: left;
}
.buy-pay p{
    background: #fff;
    padding-left: 10px;
    border-top: 1px solid #EEEEEE;
    font:14px "Microsoft yahei",Arial;
    height:40px;
    line-height: 40px;
}
.buy-pay p span{
    color: #DD0707;
}
.buy-pay a{
    width:80px;
    background: #FF3B30;
    color: #fff;
    text-align: center;
}
.buy-empty{
    /* display: none; */
    margin-top: 55px;

}
.buy-empty img{
    display: block;
    width:120px;
    height:120px;
    margin: 20px auto;
}
.buy-empty p{
    text-align: center;
}
.buy-sle{
    height:30px;
    line-height: 30px;
    font-size: 12px;
    color: #969696;
}
.buy-empty a{
    display: block;
    width:115px;
    height:30px;
    line-height: 28px;
    background: #FDE3E3;
    color: #ED3036;
    border:1px solid #ED3036;
    text-align: center;
    margin:10px auto;
    border-radius: 5px;
}
